.nectar-app {
  height: 100%;
  background: #fff;
}
.nectar-app__header {
  display: flex;
  height: 6rem;
  border-bottom: #f0f2f5 1px solid;
  background: #fff;
}
.nectar-app__sub-header {
  background: #fff;
  border-bottom: #f0f2f5 1px solid;
}
.nectar-app__logo {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 12.6rem;
}
.nectar-app__logo img {
  width: 100%;
  height: auto;
}
.nectar-app__content {
  padding: 1rem 3rem;
  height: 100%;
  background: #fff;
}
